.stats{
    background-color:#fff;
    padding: 25px 8% 50px 8%;
    &__main-title{
        margin-top: 0;
        text-align: center;
    }
    &__list{
        display: flex;
        justify-content: space-around;
        flex-wrap: wrap;
    }
    &__item{
        display: flex;
        flex-direction: column;
        align-items: center;
        width: 20%;
        background-color:#EFF0F3;
        border-radius: 5px;
        padding: 20px 50px;
        margin-top: 30px;
        &--reviews{
            align-self: center;
        }
    }
    &__wrap{
        display: flex;
        align-items: center;
    }
    &__title{
        margin-left: 20px;
        font-size: 1.5rem;
    }
    &__image{
        width: 100px;
    }
    &__count{
        margin: 0 auto;
        font-size: 2rem;
        font-weight: bold;
    }
    &__count--people::before{
        font-family: "Font Awesome 5 Free"; 
        font-size: 1.75rem;
        font-weight: 900; 
        content: "\f0c0";
        color: #FF8E3C;
    }
    &__count--courses::before{
        font-family: "Font Awesome 5 Free"; 
        font-size: 1.75rem;
        font-weight: 900; 
        content: "\f02d";
        color: #FF8E3C; 
    }
    &__count--reviews::before{
        font-family: "Font Awesome 5 Free"; 
        font-size: 1.75rem;
        font-weight: 900; 
        content: "\f004";
        color: #FF8E3C; 
    }
    &__sign-up-btn{
        margin: 0 auto;
    }
}

@media (max-width: 1024px){
    .stats{
        padding: 25px 30px 50px 30px;
        &__item{
            padding: 15px 38px;
        }
    }
}

